import uniRate from '@/components/uni-rate/uni-rate.vue'
import uniLoadMore from "@/components/uni-load-more/uni-load-more.vue"
export default{
	components:{
		uniRate,
		uniLoadMore
	},
	data() {
		return {
			operate:false,
			uid:'',
			token:'',
			active:1,
			tabActive:0,
			sh:'',
			dataList:[],
			page:1,
			pageSize:8,
			total:0,
			more:''
		}
	},
	computed: {
	     noMore () {
	       return this.dataList.length >= this.total
	     },
	   },
	   mounted(){
	   	var that=this
		var userInfo=uni.getStorageSync('userInfo')
		if(userInfo!='' & userInfo!=null & userInfo!=undefined){
			this.uid=userInfo.uid
			this.token=userInfo.token
			this.getList(this.page)
		}
	   	setTimeout(function(){
	   		that.$getHeight('#sv',(res) =>{
	   			that.sh=res
	   		})
	   	},0)
	   },
	   onNavigationBarButtonTap(){
	   	this.toggleOperation()
	   },
	methods: {
		distributionOrderdetail(){
			uni.navigateTo({
				url:'/pages/distributionOrderdetail/distributionOrderdetail'
			})
		},
		toShop(id){
			uni.navigateTo({
				url:`/pages/bussiness/shopPreview?id=${id}`
			})
		},
		to(where){
			uni.navigateTo({
				url:`/pages/retail/${where}`
			})
		},
		toggleTab(t){
			this.tabActive=t
		},
		back(){
			uni.navigateBack({
				delta:1
			})
		},
		toggle(t){
			this.active=t
			this.reset()
			this.getList(this.page)
		},
		reset(){
			this.page=1
			this.total=0
			this.dataList=[]
			this.more=''
		},
		getList(p){
			var that=this
			var params={
				uid:this.uid,
				token:this.token,
			  page:p,
			  pagesize: this.pageSize
			}
			if(this.page==1){
				this.$loading()
			}
			  var url='&r=api.member.collection'
			  this.$apiPost(url,params).then((res) =>{
				  that.total=res.total
				  that.dataList=that.dataList.concat(res.data)
				  that.more=''
				  if(that.page==1){
				  	uni.hideLoading()
				  }
			  })
		},
		toBottom(){
			if(this.noMore){
				this.more='noMore'
				return;
			}
			var that=this
			this.more='loading'
		  // setTimeout(function(){
			  that.page++
			  that.getList(that.page)
		  // },2000)
		},
		toggleOperation(){
			this.operate=!this.operate
		},
		deleteCollect(id,from){
			var that=this
			var params={
			  uid:this.uid,
			  token: this.token,
				merchid:id
			}
			  var url='&r=api.member.collection.remove'
			  this.$apiPost(url,params).then((res) =>{
					// that.options[2].info++
					that.$msg('移除成功')
					that.dataList.splice(from,1)
			  })
		},
	}
}
